.equipo {
	width: 100%; 
	padding: 32px;
	box-sizing: border-box;
	text-align: center;
	font-size: 32px;
	position: relative;
}

.input-color {
	width: 25px;
	position: absolute;
	top: 20px;
	right: 50px;
	padding: 0;
	border: none;
}

.equipo h3 {
	font-weight: 400;
	color: #212121;
	border-bottom: 4px solid;
	display: inline-block;
	padding-bottom: 8px;
}

.colaboradores {
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;

}