.colaborador {
	width: 280px;
	margin-bottom: 24px;
	position: relative;
}

.colaborador .encabezado {
	background-color: #f0f0f0;
	border-radius: 10px 10px 0 0;
}

.colaborador img {
	width: 100px;
	border-radius: 50%;
	position: relative;
	bottom: -50px;
}

.colaborador .info {
	background: #ffffff;
	box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.08);
 	border-radius: 0 0 10px 10px;
 	padding-top: 90px;
 	padding-bottom: 40px;
 	min-height: 100px;
 	font-family: 'Monteserrat', sans-serif;
}

.colaborador .info h4 {
	color: #6278f7;
	font-size: 18px;
	line-height: 22px;
	font-weight: bold;
	margin-bottom: 8px;
}

.colaborador .info h5 {
	font-size: 18px;
	line-height: 22px;
	color: #212121;
	padding: 0 24px;
	font-weight: 400;
	margin: 0;
}

.eliminar{
    position: absolute;
    right: -15px;
    top: -15px;
}